.WarningModal {
    width: 100vw;
    height: 100vh;
    color: red;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #000;
}

.WarningModal h1 {
    width: 50%;
}

.InstructionsModal {
    width: 100vw;
    height: 100vh;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: #ffffff94;
}

.Instructions ul {
    margin: 2rem 0 0 2rem;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}

.InstructionsModal h1 {
    color: rgb(45, 124, 243);
}
